.mobile {
  background-color: white;
  height: 100vh;
  width: 60vw;
  position: absolute;
  top: -6px;
  padding: 10px 12px;
  right: 0px;
  flex-direction: column;
}

.close {
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
}

.icon {
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
}

.mobile-options {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 24px;
  justify-content: centre;
  align-content: center;
  height: 100%;
}

.mobile-option {
  font-size: 24px;
  margin: 12px 0px;
  display: flex;
}

.mobile-option a {
  text-decoration: none;
  display: flex;
  color: black;
}
